html {
  background-size: cover;
  font-family: "helvetica neue";
  text-align: center;
  font-size: 10px;
}

body {
  margin: 0;
  font-size: 2rem;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
      align-items: center;
}

.clock {
  background-image: url(./images/capsshield.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 250px 250px;
  position: relative;
  width: 20rem;
  height: 20rem;
  border: 1px solid white;
  border-radius: 50%;
  margin: 50px auto;
  position: relative;
  /* padding: 2rem; */
  -webkit-box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.1), inset 0 0 0 3px #efefef,
    inset 0 0 10px black, 0 0 10px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.1), inset 0 0 0 3px #efefef,
    inset 0 0 10px black, 0 0 10px rgba(0, 0, 0, 0.2);
}

.clock-face {
  width: 100%;
  height: 100%;
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
}

.secondhand {
  width: 50%;
  height: 2%;
  background: red;
  position: absolute;
  top: 50%;
  -webkit-transform-origin: 100%;
      -ms-transform-origin: 100%;
          transform-origin: 100%;
  -webkit-transform: rotate(270deg);
      -ms-transform: rotate(270deg);
          transform: rotate(270deg);
  -webkit-transition: all 0.05s;
  -o-transition: all 0.05s;
  transition: all 0.05s;
  -webkit-transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
       -o-transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
          transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
}

.hidden {
  visibility: hidden;
}

.white {
  color: white;
}

.green {
  color: green;
}

.red {
  color: red;
}

#answerBox {
  font-size: 30px;
}

.left-col {
  float: left;
  width: 50%;
}

.right-col {
  float: right;
  width: 50%;
}

#startBtn {
  margin: 20px;
  background-color: blue;
  color: white;
  border: blue;
  font-size: 20px;
  font-weight: bold;
  padding: 20px;
}



